import React, { useState, useEffect } from 'react'
import "./home.css";
import Products from "./Products/Products";

import Door1 from "../images/doors.jpg";
import Door2 from "../images/door.png";
import Door3 from "../images/door3.jpg";
import Door4 from "../images/door4.jpg";



export default function Home(props) {

    const [i, setI] = useState(0);

    const view = (type) => {
        console.log(props.data[i].NAME);
        if (type === "-") {
            if (i !== 0) {
                setI(i - 1)
                console.log(i);
            } else {
                setI(props.data.length - 1);
            }
        } else {
            if (i < props.data.length - 1) {
                setI(i + 1)
            } else {
                setI(0);
            }

        }
    }


    return (
        <div className="home" >

            <div className="home-child">
                {props.data.length > 0 ? <Products name={props.data[i].NAME} img={props.data[i].IMAGE} view={view} /> : <h2>לא נמצא דגם</h2>}
            </div>
        </div>
    )
}
