import React, { useState, useEffect } from 'react'
import './App.css';
import { HashRouter, Switch, Route } from 'react-router-dom';


import Header from "./Header/Header"
import Home from "./Home/Home"
import About from "./About/About";
import Contect from "./Contect/Contect";
import Footer from "./Footer/Footer"
import Data from "./data/doors.json"

function App() {

  const [data, setData] = useState(Data.catalog);
  const [dataToPrint, setDataToPrint] = useState(Data.catalog);
  const [styleNav, setStyleNav] = useState("");
  const [flag, setFalg] = useState(false);



  const search = (e) => {
    if (e) {
      let searchData = data.filter((element) => element.NAME.indexOf(e) >= 0);
      if (searchData) {
        setDataToPrint(searchData);
      } else {
        setDataToPrint('')
      }
    } else {
      setDataToPrint(data);
    }
  }
  // if (flag) {
  //   window.onscroll = (e) => {
  //     // debugger

  //     if (document.body.scrollTop > 300 || document.documentElement.scrollTop > 300) {
  //       console.log('work');
  //       setStyleNav("block");
  //     } else {
  //       setStyleNav("none");
  //     }
  //   }
  // }


  return (
    <div className="App" >
      <HashRouter>
        <div className="Header">
          <Header search={search} styleNav={styleNav} />
        </div>
        <div className="body">
          <Switch>

            <Route exact path="/" component={() => {
              return <Home data={dataToPrint} />
            }} />
            <Route path="/about" component={() => {
              return <About />
            }} />
            <Route path="/contect" component={() => {
              return <Contect />
            }} />
          </Switch>
        </div>
        <div className="Footer">
          <Footer />
        </div>
      </HashRouter>
    </div>
  );
}

export default App;
