import React, { useState, useEffect } from 'react'
import "./products.css";
import 'react-awesome-slider/dist/styles.css';

import Left from "../../images/left-arrow.svg";
import Right from "../../images/right-arrow.svg";


import IdsA from "../../images/rav-barich/IdsA.png";
import IdsB from "../../images/rav-barich/IdsB.png";
import IdsC from "../../images/rav-barich/IdsC.png";
import IdsD from "../../images/rav-barich/IdsD.png";
import IdsE from "../../images/rav-barich/IdsE.png";
import IdsF from "../../images/rav-barich/IdsF.png";
import IdsG from "../../images/rav-barich/IdsG.png";
import IdsH from "../../images/rav-barich/IdsH.png";
import IdsI from "../../images/rav-barich/IdsI.png";
import IdsJ from "../../images/rav-barich/IdsJ.png";
import IdsK from "../../images/rav-barich/IdsK.png";
import IdsL from "../../images/rav-barich/IdsL.png";


export default function Products(props) {
    var myInterval
    const [img, setImg] = useState()
    const [opacity, setOpacity] = useState(1);
    const [imgPath, setImgPath] = useState("../../images/rav-barich/IdsI.png")
    var propsImage = props.img;
    var i = 9;
    var my;
    useEffect(
        () => {

            if (propsImage === "IdsA") {
                setImg(IdsA);
            } if (propsImage === "IdsB") {
                setImg(IdsB);
            }
            if (propsImage === "IdsC") {
                setImg(IdsC);
            }
            if (propsImage === "IdsD") {
                setImg(IdsD);
            }
            if (propsImage === "IdsE") {
                setImg(IdsE);
            }
            if (propsImage === "IdsF") {
                setImg(IdsF);
            }
            if (propsImage === "IdsG") {
                setImg(IdsG);
            }
            if (propsImage === "IdsH") {
                setImg(IdsH);
            }
            if (propsImage === "IdsI") {
                setImg(IdsI);
            }
            if (propsImage === "IdsJ") {
                setImg(IdsJ);
            }
            if (propsImage === "IdsK") {
                setImg(IdsK);
            }
            if (propsImage === "IdsL") {
                setImg(IdsL);
            }
        }
    )
    const notFound = () => {
        if (props.name.length > 0) {
            return (
                <div style={{ opacity: `${opacity}` }} className="product">
                    <div className="right">
                        <img src={Right} id="right" onClick={() => {
                            props.view("-")
                        }} />
                    </div>
                    <div className="product-child">
                        <div className="name">
                            <h3>{props.name}</h3>
                        </div>
                        <div className="img"   >
                            <img id="img" src={img} alt="p" />
                        </div>
                    </div>
                    <div className="left"><img src={Left} id="left" onClick={() => {
                        props.view("+")
                    }} /></div>
                </div >
            )
        } else {
            return (<div className="notFound">
                <h1>לא נמצא דגם</h1>
            </div>)
        }
    }

    return (
        <div className="Products">
            {notFound()}
        </div>
    )
}
